.nav-menu {
	padding: 18px 0;
}
.nav-menu li {
	float: left;
}
.nav-menu li:last-child {
	padding-right: 125px;
}
.toggle-btn {
	margin-top: 10px;
	width: 100%;
	padding: 10px 0;
	font-size: 19px;
	color: #fff;
	background: #091b43;
	text-align: center;
	text-transform: uppercase;
	outline: none;
	border: none;
	font-weight: 900;
}
.nav-menu a {
	display: block;
	padding: 10px 20px;
	font-size: 19px;
	text-transform: uppercase;
	color: #fff;
}
.nav-menu li:hover {
    background-color: #000;
    transition: 0.3s;
}

.nav-menu-toggle {
	padding: 18px 0;
	transition: 0.3s;
}
.nav-menu-toggle li {
	float: left;
}
.nav-menu-toggle li:last-child {
	padding-right: 125px;
}
.nav-menu-toggle a {
	display: block;
	padding: 10px 20px;
	font-size: 19px;
	text-transform: uppercase;
	color: #fff;
}
.nav-menu-toggle li:hover {
    background-color: #000;
    transition: 0.3s;
}