.logo-brand {
	max-width: 100%;
	font-size: 43px;
	width: 190px;
}
.logo-brand span:nth-child(1), span:nth-child(3), span:nth-child(5) {
	background-color: #00aedb;
	color: #fff;
}
.logo-brand span:nth-child(1), span:nth-child(3) {
	padding: 16px 0 0;
}
.logo-brand span:nth-child(2), span:nth-child(4), span:nth-child(6) {
	background-color: #b5da06;
	color: #fff;
}
.logo-brand span:nth-child(2) {
	padding: 16px 0 0;
}