@media only screen and (min-device-width: 320px) and (max-device-width: 480px){
    .container {
        padding: 0;
    }
    .header-wrap .row .col-2 {
        width: 50%;
    }
    .header-wrap .row .col-7 {
        width: 50%;
    }
    .search-wrap button {
        font-size: 10px;
        padding: 0 0;
        height: none;
    }
    .search-wrap input {
        width: 120px;
    }
    .social-btn-wrap {
        display: none;
    }
    .nav-menu-toggle li {
        width: 100%;
    }
    .nav-menu ul li {
        display: none;
    }
    .top-stories p {
        font-size: 30px;
        padding: 0;
        padding-bottom: 10px;
        text-align: center;
    }
    .top-post-style .top-story-image {
        width: 100%;
    }
    .top-post-style #top-post-category {
        width: 100%;
        margin-top: -10px;
    }
    .top-post-style .top-story-title {
        font-size: 20px;
    }.c-post-date {
        margin-left: 60px;
    }
    .c-post-wrap {
        padding: 0;
        margin: 15px 0;
    }
    .post-title {
        margin-left: 60px;
    }
    .post-title, .post-title a {
        margin-top: 0;
        text-align: center;
        font-size: 25px;
        line-height: 25px;
        margin-bottom: 30px;
    }
    .post-description {
        padding: 10px 0;
        margin-left: 10px;

    }
    .image-post-wrap {
        width: 100%;
    }
    .post-image {
        width: 100%;
    }
    .sidebar {
        float: none;
    }
    .top-post-wrap .row .col-4 {
        width: 100%;
        
    }
    .row .col-6 {
    width: 100%;
    margin: 0 auto;
    }
    .row .col-8 {
        width: 100%;
    }
    .sidebar {
        display: none;
    }
    .c-post-wrap .row .col-3 {
        padding: 0px 5px;
        width: 30%;
    }
    .c-post-wrap .row .col-7 {
        width: 50%;
    }
    .c-post-wrap .row .col-2 {
        width: 20%;
    }
    .post-comments {
        margin-left: 1px;
    }
    
}
@media only screen and (min-device-width : 768px) and (max-device-width : 1024px) {
    .header-wrap .row .col-2 {
        width: 28%;
    }
    .header-wrap .row .col-7 {
        width: 40%;
    }
    .header-wrap .row .col-3 {
        width: 32%;
    } 
    .search-wrap button {
        font-size: 10px;
        padding: 0 0;
        height: none;
    }
    .search-wrap input {
        width: 200px;
    }
    .social-btn-wrap {
        float: right;
    }
    .nav-menu-toggle ul li {
        width: 100%;
    }
    .nav-menu ul li {
        display: none;
    }

    .top-post-wrap .row .col-4 {
        width: 33.33%;
    }
    .container {
        padding: 5px;
    }
    .top-post-style {
        padding: 0 5px;
    }
    .top-post-style .top-story-image {
        width: 100%;
    }
    .top-post-style #top-post-category {
        width: 100%;
        margin-top: -30px;
    }
}
@media only screen and (max-device-width : 1024px) {
    .nav-menu ul li {
        display: block;
    }
    .toggle-btn {
        display: none;
    }
}